import { HTMLControllLoader } from "./ui-components/html-controll-loader";
import { GraphManager } from "./graph/graph-manager";
import { GraphMap } from "./graph/graph-map";
import { PaperScope } from 'paper';

export const paper = new PaperScope();
export let modelOutText: paper.PointText;
export let graph: GraphMap;

export default function Sketch() {

    window.onload = getOnload();
    function zoom() {
        let tool = new paper.Tool();
        tool.onKeyDown = function (event: paper.KeyEvent) {
            // Zoom in
            if (event.key === '+') {
                paper.view.zoom *= 1.1;
            }
            // Zoom out
            else if (event.key === '-') {
                paper.view.zoom /= 1.1;
            }
        };
    }

    function getOnload() {
        return () => {
            paper.install(window);
            paper.setup('paper-canvas');
            paper.view.viewSize = new paper.Size(window.innerWidth - 50, window.innerHeight - 200);

            zoom();
            graph = GraphManager.createDefaultGraph();
            HTMLControllLoader.addControlElements(() => paper.project.clear());
        };
    }


    // Most return null
    return null;

}
