export class ActionButton {
    readonly button: HTMLInputElement;

    constructor(elementId: string, buttonText: string, className: string | null = null, onClickFunction: Function) {
        this.button = document.createElement("input") as HTMLInputElement
        this.button.id = elementId;
        this.button.type = 'button';
        this.button.value = buttonText;
        if (className !== null) this.button.className = className;
        this.button.onclick = () => onClickFunction.apply(null);
    }
}
