import { GraphDtoModels } from "../graph/graph-dto-models";

export module MessagesForGPT {

    export function getTextToGraphMessages(text: string) {
        if (text.trim() === "") throw new Error("empty string..");
        return [
            { role: 'user', content: graphExample },
            { role: 'user', content: instructionsTextToGrpah },
            { role: 'user', content: text }
        ];
    }

    export function getQAMessages(question: string) {
        if (question.trim() === "") throw new Error("empty string..");
        return [
            { role: 'user', content: graphExample },
            { role: 'user', content: instructionsForQA },
            { role: 'user', content: question }
        ];
    }


    export function getMessagesForNodeExpansion(graph: GraphDtoModels.GraphDto, selectedNode: GraphDtoModels.NodeDto) {
        return [
            { role: 'user', content: graphExample },
            { role: 'user', content: getInstructionsForNodeExpansion(graph, selectedNode) }
        ];
    }

    const graphExample: string = '' +
        'This is graph model: ' +
        'class Graph {\n' +
        '\tnodes: Node[];\n' +
        '\tedges: Edge[];\n' +
        '}\n' +
        '\n' +
        'class Node {\n' +
        '\tid: number;\n' +
        '\ttext: string;\n' +
        '\tdescription: string|null;\n' +
        '}\n' +
        '\n' +
        'class Edge {\n' +
        '\tnodeFrom: number;\n' +
        '\tnodeTo: number;\n' +
        '\ttext: string|null;\n' +
        '}\n\n'

    const instructionsTextToGrpah: string = '' +
        '0. Summarize provided text and make knowledge graph from it.\n' +
        '1. Response must be in json format.\n' +
        '2. Json must be of "Graph" type.\n' +
        '3. Graph must be "small" with node count should be less or equal to 10!\n' +
        '4. No additional text should present response besides json!.\n' +
        '5. Node text should be short. 1 or 2 words.\n' +
        '6. Node can have description.\n' +
        '7. Edge must contain text with relevant info.\n' +
        '8. Edge text can not blindly repeat text contained by node text or description.\n' +
        '9. Nodes should be connected by relevance.\n' +
        '10. Text goes here:\n\n'

    const instructionsForQA: string = '' +
        '0. Answer the question.\n' +
        '1. Summarize answer text and make knowledge graph from it.\n' +
        '2. Response must be in json format.\n' +
        '3. Json must be of "Graph" type.\n' +
        '4. Graph must be "small" with node count be less or equal to 10!\n' +
        '5. No additional text should present response besides json!.\n' +
        '6. Node text should be short. 1 or 2 words.\n' +
        '7. Node can have description.\n' +
        '8. Edge must contain text with relevant info.\n' +
        '9. Edge text can not blindly repeat text contained by node text or description.\n' +
        '10. Nodes should be connected by relevance.\n' +
        '11. Question goes here:\n\n';


    export function getInstructionsForNodeExpansion(graph: GraphDtoModels.GraphDto, selectedNode: GraphDtoModels.NodeDto) {
        let id = Math.max(...graph.nodes?.map(e => e.id)!) + 1;
        return '' +
            '0. Expand on selectd graph node by creating small sub-graph.\n' +
            '1. Summarize answer text and make knowledge graph from it.\n' +
            '2. Response must be in json format.\n' +
            '3. Json must be of "Graph" type.\n' +
            '4. Sub-graph must be "small" with node count from 1 to 3!\n' +
            '5. Sub-graph can not reapet main grapht info!\n' +
            '6. Sub-graph node id\'s should start from: ' + id + '\n' +
            // '7. Sub-graph should contain edge from selected node with id: ' + (id - 1) + ' to most relevant new node.\n' +
            '8. No additional text should be present in response besides json!.\n' +
            '9. Node text should be short. 1 or 2 words.\n' +
            '10. Node can have description.\n' +
            '11. Edge must contain text with relevant info.\n' +
            '12. Edge text can not blindly repeat text contained by node text or description.\n' +
            '13. Nodes should be connected by relevance.\n' +
            '14. Selected node\'s id is: ' + selectedNode.id + '\n' +
            '15. Main graph goes here:\n\n' + JSON.stringify(graph);
    }
}