import { GraphDtoModels } from "./graph-dto-models";
import { DemoGraph } from "./demo-graph";
import { GraphMap } from "./graph-map";
import { paper } from "../sketch";

export module GraphManager {
    const fontSize = 10;
    import GraphDto = GraphDtoModels.GraphDto;
    export type Pair<T, U> = [T, U];

    function addTextAtMidPoint(line: paper.Path.Line, content: string) {
        let midpoint = line.getPointAt(line.length / 2);

        let text = new paper.PointText({
            content: content,
            point: midpoint,
            justification: 'center',
            fontSize: fontSize
        });
        text.position.y -= 10;
        text.fillColor = new paper.Color(0.77, 0.25, 0.25);
        text.sendToBack();

        let rect: paper.Shape.Rectangle | null;

        let linePosition = line.position.clone();
        text.onFrame = () => {
            if (rect != null) return;
            if (line.position.equals(linePosition)) return;
            linePosition = line.position.clone();
            text.position = line.getPointAt(line.length / 2);
            text.position.y -= 10;
        }
    }

    export function getLine(p0: paper.Point, p1: paper.Point, text: string | null) {
        let line = new paper.Path.Line(p0, p1);
        line.strokeWidth = 1;
        line.strokeColor = new paper.Color(0, 0, 0);
        line.fillColor = new paper.Color(0, 0, 0);
        if (text != null) addTextAtMidPoint(line, text);
        line.sendToBack();
        return line;
    }

    export function createGraphFrom(json: string): GraphMap | null {
        let dto: GraphDtoModels.GraphDto | null = null;
        try {
            dto = GraphDtoModels.GraphDto.fromJsonString(json);
        } catch (e) {
            console.error(e);
        }
        if (dto === null) return null;
        let g = new GraphMap();
        g.init(dto);
        return g;
    }

    export function createDefaultGraph(): GraphMap {
        let graphMap = new GraphMap();
        let graph = DemoGraph.GetGraphAboutApp();
        graphMap.init(graph);
        return graphMap;
    }

    export function createGraph(graph: GraphDto): GraphMap {
        let graphMap = new GraphMap();
        graphMap.init(graph);
        return graphMap;
    }
}
