export class TextBoxV2 {
    readonly textArea: HTMLTextAreaElement;

    constructor(elementId: string, className: string = 'reactive-textarea', onEnterKeydown: Function) {
        this.textArea = document.createElement("textarea") as HTMLTextAreaElement
        this.textArea.id = elementId;
        this.textArea.placeholder = 'Enter the text to be used for generating the graph\nor enter grapth in json format:\n{ "nodes": [{ "id": 1, "text": "node1"}, { "id": 2, "text": "node2", "description": "node description" }],\n  "edges": [{ "nodeFrom": 1, "nodeTo": 2, "text": "node1 likes node2" }]}';
        this.textArea.className = className;
        this.textArea.addEventListener('keydown', this.getListener(onEnterKeydown));
    }

    private getListener(onEnterKeydown: Function) {
        return (event: KeyboardEvent) => {
            if (event.key !== 'Enter') return;
            if (event.shiftKey) return;
            onEnterKeydown.apply(null);
        };
    }
}