export class SecureTextBox {
    readonly apiKeyInput: HTMLInputElement;

    constructor(elementId: string) {
        this.apiKeyInput = document.createElement("input") as HTMLInputElement
        this.apiKeyInput.id = elementId;
        this.apiKeyInput.className = 'api-key-textarea';
        this.apiKeyInput.type='password';
        this.apiKeyInput.placeholder='Enter your OpenAI API key here. The API key will not be stored or sent anywhere.'
    }
}
