import { turboStreamV2 } from "../chat-gpt/gpt-turbo-stream-v2";
import { GraphManager } from "../graph/graph-manager";
import { SecureTextBox } from "./secure-text-box";
import { DemoGraph } from "../graph/demo-graph";
import { ActionButton } from "./action-button";
import { TextBoxV2 } from "./text-box-v2";


export class HTMLControllLoader {

    static addControlElements(clearPaperF: Function) {
        let helpTextLabelDiv = this.getHelpTextLabelDiv();
        let openAiCallDiv = this.getOpenAiCallDiv();

        let f1 = () => GraphManager.createGraph(DemoGraph.GetFlexShapeGraph_skills());
        let f2 = () => GraphManager.createGraph(DemoGraph.GetDemoGraphV2());
        let f3 = () => GraphManager.createGraph(DemoGraph.GetGraphAboutAzureScopes0());
        let f4 = () => GraphManager.createGraph(DemoGraph.GetDemoGraph_war_ukraina());

        let f_about_1 = () => GraphManager.createGraph(DemoGraph.GetAboutMe());
        let f_about_2 = () => GraphManager.createGraph(DemoGraph.GetAboutMe2());
        let f_about_3 = () => GraphManager.createGraph(DemoGraph.GetAboutMe3());
        let f_about_4 = () => GraphManager.createGraph(DemoGraph.GetAboutMe4());
        let f_about_app = () => GraphManager.createGraph(DemoGraph.GetGraphAboutApp());

        this.addToRoot(openAiCallDiv);
        this.addToRoot(new SecureTextBox('api-key').apiKeyInput);

        this.addToRoot(new ActionButton('btn-5', 'About this app', null, f_about_app).button);
        this.addToRoot(new ActionButton('graph-1-button', 'Flex Shape skills', null, f1).button);
        this.addToRoot(new ActionButton('graph-3-button', 'Azure ..', null, f2).button);
        this.addToRoot(new ActionButton('graph-4-button', 'Azure Scopes', null, f3).button);
        this.addToRoot(new ActionButton('graph-4-button', 'war / ukraine', null, f4).button);

        this.addToRoot(new ActionButton('btn-1', 'me', null, f_about_1).button);
        this.addToRoot(new ActionButton('btn-2', 'me', null, f_about_2).button);
        this.addToRoot(new ActionButton('btn-3', 'me', null, f_about_3).button);
        this.addToRoot(new ActionButton('btn-4', 'me', null, f_about_4).button);

        this.addToRoot(new ActionButton('clear-graph-button', 'Clear paper', null, () => clearPaperF.apply(null)).button);
        this.addToRoot(helpTextLabelDiv);
    }

    static addToRoot(element: HTMLElement) {
        document.getElementById("root")?.appendChild(element);
    }

    static getHelpTextLabelDiv() {
        let helpTextLabel = document.createElement('label');
        let helpTextLabelDiv = document.createElement('div');
        helpTextLabelDiv.appendChild(helpTextLabel);
        helpTextLabelDiv.className = 'label-div';
        helpTextLabel.textContent = 'click buttons above to see some demo graph | to zoom in or zoom out, use keys \'+\' and \'-\' ';
        return helpTextLabelDiv;
    }

    static getOpenAiCallDiv() {
        let gptCallTextBox = new TextBoxV2('input-text', 'api-call-textarea', () => this.createGraphOrMakeOpenAiCall());
        let callOpenAiButton = new ActionButton('call-open-ai-btn', 'send', 'api-call-button', () => this.createGraphOrMakeOpenAiCall());
        let openAiCallDiv = document.createElement('div');
        openAiCallDiv.appendChild(gptCallTextBox.textArea);
        openAiCallDiv.appendChild(callOpenAiButton.button);
        return openAiCallDiv;
    }

    static createGraphOrMakeOpenAiCall() {
        let textArea = document.getElementById('input-text') as HTMLTextAreaElement;
        let button = document.getElementById('call-open-ai-btn') as HTMLInputElement;

        let inputValue = textArea.value.trim();
        if (inputValue == null || inputValue === "") return;

        try {
            //just try to parse ..
            JSON.parse(inputValue);
            GraphManager.createGraphFrom(inputValue);
            textArea.value = '';
        } catch (error) {
            //...
            button.value = 'sending .. ';
            turboStreamV2(inputValue).then(response => {
                console.log(response);

                response = response.replaceAll('```json', '');
                response = response.replaceAll('```', '');

                GraphManager.createGraphFrom(response);
                button.value = 'send';
                textArea.value = '';
            });
        }
    }
}
