// export module LocalUtils {
    export class List<T> {
        readonly items: Array<T>;

        constructor(it: Array<T> | null) {
            this.items = it == null ? [] : it;
        }

        size(): number {
            return this.items.length;
        }

        add(value: T): void {
            this.items.push(value);
        }

        addAll(array: Array<T>) {
            array.forEach(e => this.add(e));
        }

        get(index: number): T {
            return this.items[index];
        }
    }
// }