import { ModelStreamResponse } from "./chat-completion-stream";
import { MessagesForGPT } from "./messages-for-gpt";

export const turboStreamV2 = makeChatCompletionRequest;

const decoder = new TextDecoder();
const apiUrl = 'https://api.openai.com/v1/chat/completions';

async function makeChatCompletionRequest(content: string) {
    if (content.trim() === "") throw new Error("empty string..");

    const apiKeyInput = document.getElementById('api-key') as HTMLInputElement;
    const OPENAI_API_KEY = apiKeyInput.value;
    if (OPENAI_API_KEY === '') throw new Error('OPENAI_API_KEY is empty');
    let messages = MessagesForGPT.getQAMessages(content);

    const body = JSON.stringify({ model: 'gpt-3.5-turbo', messages: messages, stream: true });
    const headers = { 'Content-Type': 'application/json', 'Authorization': 'Bearer ' + OPENAI_API_KEY };
    const fetchData = { method: 'POST', headers: headers, body: body };

    return fetch(apiUrl, fetchData)
        .then(getResponseBodyF())
        .then(getResultToConsumerF1(extractContent))
        .then(getStreamToResponseF())
        .then(getResultToConsumerF2(extractContent));
}

function getResponseBodyF() {
    return (response: Response): ReadableStream<Uint8Array> | null => {
        if (!response.ok) {
            throw new Error("HTTP error " + response.status);
        }
        return response.body;
    };
}


function getResultToConsumerF1(extractContent: (str: string, resultConsumer: (result: string | undefined) => void) => void):
    ((value: ReadableStream<Uint8Array> | null) => ReadableStream<any> | PromiseLike<ReadableStream<any>>) | null | undefined {
    return rb => {
        if (rb == null) throw new Error("rb == null");
        let reader = rb.getReader();
        return new ReadableStream({
            start(controller) {
                function push() {
                    reader.read().then(({ done, value }) => {
                        if (done) {
                            controller.close();
                            return;
                        }
                        controller.enqueue(value);
                        push();
                        let str: string = decoder.decode(value);
                        const doNothingF = (_: string | undefined): void => { };
                        extractContent(str, doNothingF);
                    });
                }

                push();
            }
        });
    };
}


function getStreamToResponseF(): ((value: ReadableStream<any>) => string | PromiseLike<string>) | null | undefined {
    return stream => new Response(stream, { headers: { "Content-Type": "text/html" } }).text();
}

function getResultToConsumerF2(extractContent: (str: string, resultConsumer: (result: string | undefined) => void) => void):
    ((value: string) => string | PromiseLike<string>) | null | undefined {
    return result => {
        let s = "";
        let f = (e: string | undefined) => s = s + e;
        extractContent(result, f);
        return s;
    };
}

function extractContent(str: string, resultConsumer: (result: string | undefined) => void) {
    str.split("\n")
        .map(e => e?.split("data:")[1]?.trim())
        .filter(e => e != null)
        .map(e => {
            try {
                return JSON.parse(e) as ModelStreamResponse;
            } catch (e) {
                return null;
            }
        })
        .filter(e => e != null)
        .map(e => e?.choices?.[0])
        .map(e => e?.delta?.content)
        .filter(e => e !== undefined)
        .forEach(e => resultConsumer(e));
}
