import { turboStreamV2 } from "../chat-gpt/gpt-turbo-stream-v2";
import { MessagesForGPT } from "../chat-gpt/messages-for-gpt"
import { Color, Point, PointText } from 'paper';
import { GraphManager } from "./graph-manager";
import { GraphMap } from "./graph-map";
import { paper } from "../sketch";
import { List } from "./list";
import { GraphDtoModels } from "./graph-dto-models";



export class Node {

    readonly nodeDto: GraphDtoModels.NodeDto;
    readonly rect: paper.Path.Rectangle;
    // private id: number | null;
    private fontSize: number = 10;
    private pointText: paper.PointText;
    private edges = new List<paper.Segment>(null);
    private isDragging = false;
    private name: string;
    private nodeMap = new Map<Node, Node[]>();
    private graphMap: GraphMap;

    constructor(graphMap: GraphMap, nodeDto: GraphDtoModels.NodeDto) {
        this.graphMap = graphMap;
        this.nodeMap = graphMap.nodeMap;
        this.nodeDto = nodeDto;
        // this.id = nodeDto.id;
        this.name = nodeDto.text + '\n\n' + nodeDto.description;
        this.pointText = this.createPointText(this.name, this.fontSize);
        this.rect = this.createRectangle(this.pointText);
        this.pointText.content = this.name;
        this.pointText.bringToFront();
    }

    init() {
        this.pointText.onClick = () => this.showToolBox();
        this.pointText.onMouseDrag = (event: { point: paper.Point; }) => this.onDrag(event);
        this.rect.onFrame = () => this.updateNodePosition();
        this.pointText.onMouseLeave = () => this.isDragging = false;
        this.rect.onMouseLeave = () => this.isDragging = false;
    }

    toolBox: paper.PointText[] | null = null;

    private showToolBox() {
        if (this.isDragging) return;

        if (this.toolBox !== null) {
            this.removeToolBox();
            return;
        }

        let point = this.getToolBoxPosition(this.rect.bounds);
        const text1 = this.createToolBoxItem(point, 'tool box\n-----\n', false);
        const text2 = this.createToolBoxItem(text1.bounds.bottomLeft, 'remove node\n', true);
        const text3 = this.createToolBoxItem(text2.bounds.bottomLeft, 'expand (ask gpt..)', true);

        text2.onClick = () => this.removeNodeFromGraph();
        text3.onClick = () => this.callGptToExpandNode(text3);

        this.toolBox = [];
        this.toolBox.push(text1);
        this.toolBox.push(text2);
        this.toolBox.push(text3);
    }

    private callGptToExpandNode(pointText: paper.PointText) {
        pointText.content = 'expand (ask gpt..): call in progress ..';
        let dto = this.graphMap.getGraphDto()!;
        let message = MessagesForGPT.getMessagesForNodeExpansion(dto, this.nodeDto);
        console.log(message);
        let txt = JSON.stringify(message);
        turboStreamV2(txt).then(response => this.expandNode(response, pointText));
    }

    private expandNode(response: string, pointText: paper.PointText) {
        console.log(response);
        response = response.replaceAll('```json', '');
        response = response.replaceAll('```', '');
        this.graphMap.expandGraph(this.nodeDto.id, response);
        pointText.content = 'expand (ask gpt..)';
    }

    private removeToolBox() {
        this.toolBox?.forEach(e => e.remove());
        this.toolBox = null;
    }

    private removeNodeFromGraph() {
        this.rect.remove();
        this.pointText.remove();
        this.edges.items.forEach(e => e.remove());
        this.removeToolBox();
        this.nodeMap.delete(this);
    }

    private createToolBoxItem(point: paper.Point, text: string, actionItem: boolean) {
        point = point.clone();
        const pointText = new PointText(point);
        pointText.justification = 'left';
        pointText.fillColor = new paper.Color(0, 0, 0);
        pointText.fontSize = this.fontSize;
        pointText.content = text;
        if (actionItem) pointText.onMouseEnter = () => this.selectToolText(pointText, true);
        if (actionItem) pointText.onMouseLeave = () => this.selectToolText(pointText, false);
        return pointText;
    }

    private updateToolBoxPosition(rect: paper.Rectangle) {
        if (this.toolBox === null) return;
        let point = this.getToolBoxPosition(rect);
        this.toolBox[0].point = point;
        this.toolBox[1].point = this.toolBox[0].bounds.bottomLeft.clone();
        this.toolBox[2].point = this.toolBox[1].bounds.bottomLeft.clone();
    }

    private selectToolText(pointText: paper.PointText, selected: boolean) {
        if (this.toolBox === null) return;

        pointText.fillColor = selected
            ? new Color(0.2, 0.3, 0.4)
            : new Color(0, 0, 0);
    }

    private getToolBoxPosition(bounds: paper.Rectangle) {
        return bounds.topRight.clone().add(new Point(10, 10));
    }

    private onDrag(event: { point: paper.Point }) {
        let p0 = this.rect.position;
        let eP = event.point;
        let x = p0.x - (p0.x - eP.x);
        let y = p0.y - (p0.y - eP.y);
        let p = new Point(x, y);

        this.updatePosition(p);
        this.isDragging = true;
    }

    private updatePosition(p: paper.Point) {
        this.rect.position = p;
        this.pointText.position = p;
        this.edges.items.forEach(e => e.point = p);
        this.updateToolBoxPosition(this.rect.bounds);
        this.pointText.content = this.getContent(p, this.name);
    }

    private getContent(p: paper.Point, text: string) {
        return text
            + "\n[ x: " + Math.round(p.x) + ", y: " + Math.round(p.y) + " ]";
    }

    getInitialPosition(): paper.Point {
        let center = paper.view.center;
        let x = center.x;
        let y = center.y;
        let r = Math.random() * 250;
        r = Math.round(r);
        x = x + r * (Math.random() > 0.5 ? 1 : -1);
        y = y + r * (Math.random() > 0.5 ? 1 : -1);
        return new Point(x, y);
    }

    createRectangle(pointText: paper.PointText) {
        let rectangle = new paper.Path.Rectangle({
            center: pointText.point,
            point: pointText.point,
            size: new paper.Size(1, 1),
            fillColor: new Color(1, 1, 1),
            strokeColor: new Color(0, 0, 0),
            bounds: this.pointText.bounds.expand(5),
        });
        return rectangle;
    }

    getCircle(rect: paper.Path.Rectangle) {
        let shape = new paper.Path.Circle({
            center: rect.position,
            radius: 100,
            fillColor: 'white',
            strokeColor: new Color(0, 0, 0),
        });

        shape.sendToBack();
        return shape;
    }

    private addConnectedNodes(key: Node, value: Node[], nodes: List<Node>) {
        if (key === this) {
            nodes.addAll(value);
        } else if (value.some(e => e === this)) {
            nodes.add(key);
        }
    }


    createPointText(text: string, fontSize: number | string = 40) {
        let point = this.getInitialPosition();
        const pointText = new PointText(point);
        pointText.justification = 'left';
        pointText.fillColor = new paper.Color(0, 0, 0);
        pointText.fontSize = fontSize;
        pointText.content = this.getContent(point, text);
        return pointText;
    }

    addEdge(segment: paper.Segment) {
        this.edges.add(segment);
    }

    lastPosition: paper.Point | null = null;

    private updateNodePosition() {
        if (this.isDragging) return;

        let connectedNodes = new List<Node>([]);
        this.nodeMap.forEach((value: Node[], key: Node) => this.addConnectedNodes(key, value, connectedNodes));
        let otherNodes = this.getOtherNodes(this.nodeMap, connectedNodes);

        let vector1 = this.rect.position;

        let resultV = connectedNodes.items
            .map(e => e.rect)
            .map(e => e.position)
            .map(e => this.getMovementVector(e, vector1))
            .reduce((a, b) => a.add(b), vector1);

        resultV = otherNodes
            .map(e => e.rect)
            .map(e => e.position)
            .map(e => this.getAvoidanceMovementVector(e, vector1))
            .reduce((a, b) => a.add(b), resultV);


        resultV = this.clip(resultV);
        this.rect.bounds = this.pointText.bounds.expand(5);
        this.updatePosition(resultV);
    }

    private getOtherNodes(nodeMap: Map<Node, Node[]>, connectedNodes: List<Node>) {
        let allNodes = new List<Node>([]);
        nodeMap.forEach((value: Node[], key: Node) => {
            allNodes.add(key);
            allNodes.addAll(value);
        });
        let notInConnectedNodes = (e: Node) => !connectedNodes.items.some(x => x === e);
        return allNodes.items.filter(notInConnectedNodes);
    }

    private desired_distanceA = 100000;
    private desired_distanceB = 50000;
    private desired_distanceC = 25000;

    private proportionsA = 900 / 15;
    private proportionsB = 500 / 13;
    private proportionsC = 500 / 15;

    private smoothStep(t: number): number {
        return t * t * (3 - 2 * t);
    }

    private getMovementVector(vector2: paper.Point, vector1: paper.Point) {
        let distance = vector2.getDistance(vector1, true);
        let d0 = distance === 0 ? 1 : distance;

        // Normalize distance for attraction (distance > desired_distanceA)
        let attraction_t = Math.max(0, Math.min(1, (distance - this.desired_distanceA) / (this.desired_distanceA * 0.5)));
        let attraction_force = this.proportionsA * (distance * 0.0001) * this.smoothStep(attraction_t);

        // Normalize distance for avoidance (distance < desired_distanceB)
        let avoidance_t = Math.max(0, Math.min(1, (this.desired_distanceB - distance) / (this.desired_distanceB * 0.5)));
        let avoidance_force = this.proportionsB * this.smoothStep(avoidance_t) / (d0 * 0.0001);

        let dirV = vector2.subtract(vector1).divide(d0);
        let attraction_vector = dirV.multiply(attraction_force);
        let avoidance_vector = dirV.multiply(-avoidance_force);

        const resultV = attraction_vector.add(avoidance_vector);
        return resultV;
    }

    private getAvoidanceMovementVector(vector2: paper.Point, vector1: paper.Point) {
        let distance = vector2.getDistance(vector1, true);
        let d0 = distance === 0 ? 1 : distance;

        // Normalize distance for avoidance (distance < desired_distanceC)
        let avoidance_t = Math.max(0, Math.min(1, (this.desired_distanceC - distance) / (this.desired_distanceC * 0.5)));
        let avoidance_force = this.proportionsC * this.smoothStep(avoidance_t) / (d0 * 0.0001);

        let dirV = vector2.subtract(vector1).divide(d0);
        let avoidance_vector = dirV.multiply(-avoidance_force);

        return avoidance_vector;
    }

    private clip(point: paper.Point) {
        let x = point.x;
        let y = point.y;

        let border = 5;
        let minX = border;
        let minY = border;
        let maxX = paper.view.bounds.width - border;
        let maxY = paper.view.bounds.height - border;

        x = x < minX ? minX : x;
        y = y < minY ? minY : y;
        x = x > maxX ? maxX : x;
        y = y > maxY ? maxY : y;

        return new Point(x, y);
    }
}