export module GraphDtoModels {

    export class GraphDto {
        nodes: NodeDto[] | undefined;
        edges: EdgeDto[] | undefined;

        static fromJsonString(json: string): GraphDto {
            return JSON.parse(json) as GraphDto;
        }

        toJsonString(): string {
            return JSON.stringify(this);
        }
    }

    export class NodeDto {
        id!: number;
        text!: string;
        description!: string;
    }

    export class EdgeDto {
        nodeFrom!: number;
        nodeTo!: number;
        text: string | null;

        constructor(nodeFrom: number, nodeTo: number, text: string | null = null) {
            this.nodeFrom = nodeFrom;
            this.nodeTo = nodeTo;
            this.text = text;
        }
    }
}